import React from "react";
import { useStaticQuery, graphql, Link } from "gatsby";

import Layout from "../components/Layout";

import { Row, Col, Card, Typography } from "antd";

const { Meta } = Card;

const IndexPage = () => {
  const posts = useStaticQuery(graphql`
    query {
      allContentfulPortfolio(limit: 10) {
        edges {
          node {
            id
            childContentfulPortfolioPortfolioDescriptionTextNode {
              portfolioDescription
            }
            portfolioAssets {
              id
              file {
                url
                fileName
              }
            }
            portfolioDescription {
              id
              portfolioDescription
            }
            portfolioTitle
            portfolioUrl
            slug
          }
        }
      }
    }
  `);

  console.log(
    posts.allContentfulPortfolio.edges[0].node.portfolioAssets[0].file.url
  );

  return (
    <Layout className="portfolio-page">
      <article id="main">
        <header>
          <h2>Portfolio</h2>
          <p>List of my featured projects</p>
        </header>

        <section className="wrapper style5">
          <div className="inner">
            <Row gutter={16}>
              {posts.allContentfulPortfolio.edges.map(el => (
                <Col sm={8} key={el.node.id} className="mb-6">
                  <Card
                    hoverable
                    cover={
                      <img
                        alt={el.node.portfolioTitle}
                        src={el.node.portfolioAssets[0].file.url}
                      />
                    }
                  >
                    <div className="text-center">
                      <Typography.Paragraph>
                        <Meta
                          title={el.node.portfolioTitle}
                          className="text-center"
                        />
                      </Typography.Paragraph>

                      <Link
                        to={`/portfolio/${el.node.slug}`}
                        className="button primary"
                      >
                        View Detail
                      </Link>
                    </div>
                  </Card>
                </Col>
              ))}
            </Row>
          </div>
        </section>
      </article>
    </Layout>
  );
};

export default IndexPage;
